import React, { useState, useEffect } from 'react';
import { bool, func, object, number } from 'prop-types';
import { styModalWrapper } from './styles';

const CLOUDINARY_CLOUD_NAME = 'ddr3jvlpu'; 
const CLOUDINARY_UPLOAD_PRESET = 'wedding-wishes'; 

function StoryModal({ isOpen, onClose, editStory, editIndex }) {
  const [formData, setFormData] = useState({
    title: '',
    date: '',
    description: '',
    imageUrl: '',
  });
  const [isUploading, setIsUploading] = useState(false);

  // Load data when editing
  useEffect(() => {
    if (isOpen && editStory) {
      setFormData({
        title: editStory.title || '',
        date: editStory.date || '',
        description: editStory.description || '',
        imageUrl: editStory.image || '',
      });
    } else if (isOpen && !editStory) {
      // Reset form when adding new
      setFormData({
        title: '',
        date: '',
        description: '',
        imageUrl: '',
      });
    }
  }, [isOpen, editStory]);

  if (!isOpen) return null;

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleImageUpload = () => {
    setIsUploading(true);

    // Khá»Ÿi táº¡o Cloudinary Upload Widget
    if (window.cloudinary) {
      const widget = window.cloudinary.createUploadWidget(
        {
          cloudName: CLOUDINARY_CLOUD_NAME,
          uploadPreset: CLOUDINARY_UPLOAD_PRESET,
          sources: ['local', 'camera'],
          multiple: false,
          maxFileSize: 2000000, // 2MB
          clientAllowedFormats: ['jpg', 'jpeg', 'png', 'gif', 'webp'],
          cropping: true,
          croppingAspectRatio: 1.5,
          croppingShowDimensions: true,
          folder: 'wedding-stories',
          resourceType: 'image',
        },
        (error, result) => {
          setIsUploading(false);
          if (error) {
            console.error('Upload error:', error);
            alert('Lá»—i upload áº£nh. Vui lÃ²ng thá»­ láº¡i.');
            return;
          }

          if (result.event === 'success') {
            const imageUrl = result.info.secure_url;
            setFormData(prev => ({
              ...prev,
              imageUrl
            }));
          }
        }
      );
      widget.open();
    } else {
      setIsUploading(false);
      alert('Cloudinary chÆ°a Ä‘Æ°á»£c táº£i. Vui lÃ²ng thá»­ láº¡i.');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!formData.title.trim() || !formData.date.trim() || !formData.description.trim()) {
      alert('Vui lÃ²ng Ä‘iá»n Ä‘áº§y Ä‘á»§ thÃ´ng tin!');
      return;
    }

    // Láº¥y danh sÃ¡ch stories hiá»‡n táº¡i tá»« localStorage
    const existingStories = JSON.parse(localStorage.getItem('stories') || '[]');

    const storyData = {
      title: formData.title.trim(),
      date: formData.date.trim(),
      description: formData.description.trim(),
      image: formData.imageUrl || '',
    };

    let updatedStories;
    
    if (editStory && editIndex !== null && editIndex !== undefined) {
      // Edit mode - update existing story
      updatedStories = [...existingStories];
      updatedStories[editIndex] = storyData;
    } else {
      // Add mode - add new story
      updatedStories = [...existingStories, storyData];
    }

    // LÆ°u vÃ o localStorage
    localStorage.setItem('stories', JSON.stringify(updatedStories));

    // Gá»i API Ä‘á»ƒ lÆ°u vÃ o file
    try {
      const response = await fetch('/api/save-stories', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(updatedStories)
      });
      
      if (!response.ok) {
        console.warn('âš ï¸ KhÃ´ng thá»ƒ lÆ°u vÃ o file, nhÆ°ng Ä‘Ã£ lÆ°u vÃ o localStorage');
      } else {
        const result = await response.json();
        console.log('âœ… ÄÃ£ lÆ°u stories vÃ o file:', result);
      }
    } catch (apiError) {
      console.warn('âš ï¸ API error:', apiError.message);
      // Váº«n tiáº¿p tá»¥c vÃ¬ Ä‘Ã£ lÆ°u vÃ o localStorage
    }

    // Dispatch custom event Ä‘á»ƒ cáº­p nháº­t StorySection
    window.dispatchEvent(new CustomEvent('storyUpdated'));

    // Reset form
    setFormData({
      title: '',
      date: '',
      description: '',
      imageUrl: '',
    });

    // ÄÃ³ng modal
    onClose();

    alert(editStory ? 'Cáº­p nháº­t cÃ¢u chuyá»‡n thÃ nh cÃ´ng! âœ¨' : 'ThÃªm cÃ¢u chuyá»‡n thÃ nh cÃ´ng! âœ¨');
  };

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div css={styModalWrapper} onClick={handleOverlayClick}>
      <div className="overlay" />
      <div className="modal-container">
        <div className="modal-header">
          <h2 className="modal-title">
            {editStory ? 'âœï¸ Chá»‰nh sá»­a CÃ¢u Chuyá»‡n' : 'ğŸ“– ThÃªm CÃ¢u Chuyá»‡n'}
          </h2>
        </div>
        
        <div className="modal-body">
          <form className="story-form" onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="title">TiÃªu Ä‘á» *</label>
              <input
                type="text"
                id="title"
                name="title"
                className="form-control"
                placeholder="VD: Jumpa Pertama"
                value={formData.title}
                onChange={handleChange}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="date">NgÃ y thÃ¡ng *</label>
              <input
                type="text"
                id="date"
                name="date"
                className="form-control"
                placeholder="VD: 10 Oktober 2015"
                value={formData.date}
                onChange={handleChange}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="description">Ná»™i dung cÃ¢u chuyá»‡n *</label>
              <textarea
                id="description"
                name="description"
                className="form-control"
                placeholder="Ká»ƒ cÃ¢u chuyá»‡n cá»§a báº¡n..."
                rows="6"
                value={formData.description}
                onChange={handleChange}
                required
              />
            </div>

            <div className="form-group">
              <label>áº¢nh (TÃ¹y chá»n)</label>
              <div style={{ marginBottom: '10px' }}>
                <button
                  type="button"
                  onClick={handleImageUpload}
                  disabled={isUploading}
                  className="upload-button"
                >
                  {isUploading ? 'â³ Äang táº£i...' : 'ğŸ“· Chá»n áº£nh'}
                </button>
              </div>
              {formData.imageUrl && (
                <div className="image-preview">
                  <img src={formData.imageUrl} alt="Preview" />
                  <button
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, imageUrl: '' }))}
                    className="remove-image"
                  >
                    âœ•
                  </button>
                </div>
              )}
            </div>

            <div className="form-actions">
              <button type="button" onClick={onClose} className="btn-cancel">
                Há»§y
              </button>
              <button type="submit" className="btn-submit">
                {editStory ? 'ğŸ’¾ LÆ°u thay Ä‘á»•i' : 'âœ¨ ThÃªm cÃ¢u chuyá»‡n'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

StoryModal.propTypes = {
  isOpen: bool.isRequired,
  onClose: func.isRequired,
  editStory: object,
  editIndex: number,
};

export default StoryModal;
